.products {
  padding: 70px 0;
}
.products .container {
  padding: 25px;
  background-color: var(--bg-color);
  border-radius: 30px;
}
.products .container .cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 50px;
}
.products .container .cards .box {
  margin: 0 auto;
  padding: 20px;
  width: 200px;
  height: 275px;
  background-color: white;
  text-align: center;
  border-radius: 30px;
  position: relative;
}
.products .container .cards .box .image img {
  width: 110px;
  height: 110px;
  border-radius: 50%;
}
.products .container .cards .box .text span {
  padding: 7px;
  margin-top: 10px;
  display: inline-block;
  font-size: 10px;
  border: 1px solid rgba(0, 0, 0, 0.47);
  border-radius: 25px;
}
.products .container .cards .box .text p {
  margin-top: 15px;
  color: var(--main-color);
  font-family: var(--playfair-font);
  font-weight: bold;
}
.products .container .cards .box .info {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.products .container .cards .box .info span {
  color: hsl(353, 93%, 50%);
  font-size: 14px;
  font-weight: bold;
}
.products .container .cards .box .info i {
  width: 22px;
  height: 22px;
  color: white;
  background-color: hsl(353, 93%, 50%);
  border-radius: 50%;
  font-size: 11px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.products i.heart {
  position: absolute;
  left: 20px;
  top: 15px;
  cursor: pointer;
}
.products i.heart.selected {
  color: red;
}
