footer {
  padding: 70px 0 10px;
}
footer .container .cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 50px;
}
@media (max-width: 767px) {
  footer .container .cards {
    text-align: center;
  }
}
footer .container .cards .box h3 {
  color: var(--main-color);
  padding-left: 5px;
  font-family: var(--playfair-font);
}
footer .container .cards .box .icons {
  display: flex;
  margin-top: 25px;
}
@media (max-width: 767px) {
  footer .container .cards .box .icons {
    justify-content: center;
  }
}
footer .container .cards .box .icons a {
  width: 30px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer .container .cards .box .icons a i {
  color: var(--main-color);
}
footer .container .cards .box ul li {
  margin-bottom: 20px;
}
footer .container .cards .box ul li a {
  color: var(--main-color);
  font-family: var(--playfair-font);
  font-size: 12px;
  font-weight: bold;
}
