.reviews {
  padding: 70px 0;
  position: relative;
}
.reviews .container {
  width: 80%;
  margin: 0 auto;
  overflow: hidden;
}
.reviews .container .cards.slider {
  display: flex;
  transition: transform 0.5s ease-in-out;
}
.reviews .container .cards .box.slide {
  min-width: 100%;
  text-align: center;
}
.reviews .container .cards .box .image {
  width: 70px;
  height: 70px;
  margin: 0 auto;
}
.reviews .container .cards .box .image img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}
.reviews .container .cards .box span {
  display: inline-block;
  width: 90%;
  margin: 20px auto;
}
.reviews .container .cards .box h3 {
  color: var(--main-color);
  width: 90%;
  margin: 0 auto 20px;
}
.reviews .container .cards .box p {
  line-height: 1.8;
  font-size: 10px;
  width: 90%;
  margin: 0 auto;
}
.reviews .container .icons {
  width: 90px;
  height: 30px;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.reviews .container .icons span {
  width: 10px;
  height: 12px;
  margin-left: 10px;
  background-color: var(--main-color);
  border-radius: 50%;
  cursor: pointer;
}
