.contacts {
  padding: 70px 0;
}
.contacts .container {
  color: white;
  height: 560px;
  background-image: url("../../assets/book.jpg");
  background-size: cover;
  text-align: center;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
.contacts .container::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgb(0 0 0 / 80%);
  border-radius: 30px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
.contacts .container h3 {
  margin-bottom: 30px;
  line-height: 1.5;
  font-size: 30px;
  font-family: var(--playfair-font);
}
.contacts .container form {
  width: 50%;
  max-width: 100%;
  height: 60px;
  margin: 0 auto;
  padding: 0 15px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 991px) {
  .contacts .container form {
    flex-direction: column;
    height: 200px;
    width: 100%;
  }
}
@media (min-width: 767px) and (max-width: 991px) {
  .contacts .container form {
    width: 70%;
  }
}
.contacts .container form div {
  width: 38%;
  height: 50%;
  font-size: 9px;
  position: relative;
}
@media (max-width: 991px) {
  .contacts .container form div {
    width: 100%;
    height: 20%;
    margin-bottom: 15px;
  }
}
.contacts .container form div input {
  width: 100%;
  height: 100%;
  padding-left: 25px;
  border: none;
  outline: none;
}
.contacts .container form div input::placeholder {
  transition: 0.3s;
}
.contacts .container form div input:focus::placeholder {
  opacity: 0;
}
.contacts .container form input[type="submit"] {
  width: 20%;
  height: 50%;
  padding: 5;
  margin-left: 10px;
  border: none;
  outline: none;
  border-radius: 30px;
  font-size: 10px;
  color: var(--container-color);
  background-color: var(--main-color);
  cursor: pointer;
}
@media (max-width: 991px) {
  .contacts .container form input[type="submit"] {
    height: 20%;
    width: 100%;
  }
}
.contacts .container form i.phone {
  color: black;
  font-size: 12px;
  position: absolute;
  left: 7px;
  top: 50%;
  transform: translateY(-50%);
}
.contacts .container form i.user {
  color: black;
  font-size: 12px;
  position: absolute;
  left: 7px;
  top: 50%;
  transform: translateY(-50%);
}
.contacts .container p {
  margin-top: 20px;
  font-size: 11px;
}
