header {
  width: 100%;
  height: 60px;
  font-family: var(--playfair-font);
  position: sticky;
  left: 0;
  top: 20px;
  z-index: 100;
}
header .container {
  height: 60px;
  background-color: black;
  border-radius: 30px;
  display: flex;
  align-items: center;
  position: relative;
}
header .container a.logo {
  padding-left: 10px;
  color: var(--container-color);
  font-size: 20px;
}
header .container nav {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  header .container nav {
    display: none;
  }
}
@media (max-width: 767px) {
  header .container nav.active {
    background-color: rgba(0, 0, 0);
    position: absolute;
    right: 0;
    top: calc(100% + 0.4px);
    width: calc(100%);
    height: 600px;
    text-align: center;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
header .container nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  header .container nav ul {
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }
}
header .container nav ul li {
  padding: 10px 30px;
}
@media (max-width: 767px) {
  header .container nav ul li {
    padding: 25px 20px;
  }
}
header .container nav ul li a {
  color: var(--text-alter-color);
  font-size: 12px;
}
@media (max-width: 767px) {
  header .container nav.active ul li a {
    font-size: 15px;
  }
}
header .container .icons {
  width: 100px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  header .container .icons {
    flex: 1;
    justify-content: flex-end;
  }
}
header .container .icons i {
  width: 30px;
  height: 30px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
header .container .icons i.xmark,
header .container .icons i.bars {
  display: none;
  font-size: 16px;
}
@media (max-width: 767px) {
  header .container .icons i.bars {
    display: flex;
  }
  header .container .icons i.bars.active {
    display: none;
  }
  header .container .icons i.xmark.active {
    display: flex;
  }
}
